.spinning_planets {
  //background-color: #ffd1dc;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  font-family: Arial, sans-serif;
  position: relative;

 &_planet-bg {
   background-color: #ffd1dc;
 }

  &_video-bg {
    background-color: #ffffff;
  }

  .title {
    font-size: 90px;
    color: white;
    text-shadow: 2px 2px 4px #4169e1, 4px 4px 0px #ff69b4;
    margin-bottom: 2rem;
  }




  .planets_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    position: relative;
  }

  .planet {
    width: 300px;
    height: 300px;
    animation: spin 10s linear infinite;
    margin-right: 120px;
    margin-top: 20px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      //margin-right: 90px;
      //margin-top: 20px;
    }

    &.blue-saturn { animation-duration: 20s; }
    &.pink-planet { animation-duration: 15s; }
    &.moon { animation-duration: 25s; }
    &.orange-saturn { animation-duration: 18s; }
    &.blue-planet { animation-duration: 22s; }
    &.green-planet { animation-duration: 17s; }
  }

  .sxme-button {
    width: 200px;
    height: 50px;
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #4169e1;
    box-shadow: #cc1fbe 0px 0px 10px;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #1e90ff;
      box-shadow: #6e0f67 0px 0px 10px;
    }
  }

  .hovo_player {
    width: 140px;
    height: 140px;
    animation: spin 10s linear infinite;
    //margin-right: 30px;
    margin-top: 36px;
  }

  .video_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    position: relative;


  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
