.birthday-screen {
  background-color: #fca5b8;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 1rem;

  .nav {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;

    .nav-group {
      display: flex;
      align-items: center;
      gap: 1rem;

      button {
        display: flex;
        align-items: center;
        gap: 0.25rem;
        color: white;
        background: none;
        border: none;
        cursor: pointer;
        font-size: 25px;
      }
    }
  }

  .content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .hb {
      width: 640px;
      height: 376px;
    }

    .characters {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-top: 2rem;

      .boy {
        width: 200px;
        height: 330px;
      }

      .girl {
        width: 230px;
        height: 330px;
      }
    }
  }

  .footer {

    display: flex;
    justify-content: center;
    align-items: center;

    .footer_img {
      width: 1710px;
      height: 134px;
    }
  }
}
