@font-face {
  font-family: "Concert_One-Regular";
  /*src: local('Manrope'), url("../public/fonts/Manrope/Manrope-SemiBold.ttf") format('truetype');*/
  src: local('Concert_One'), url("../public/fonts/Concert_One/ConcertOne-Regular.ttf") format('truetype');
}

* {
  margin: 0;
  padding: 0;
  /*box-sizing: border-box;*/
  font-family: Concert_One-Regular, sans-serif;
  /*-ms-overflow-style: none;  !* IE and Edge *!*/
  /*scrollbar-width: none;  !* Firefox *!*/
}

*::-webkit-scrollbar {
  display: none;
}

/*input, textarea {*/
/*  font-size: 16px;*/
/*  color: #061F40;*/
/*  border: none;*/
/*  outline: none;*/
/*  resize: none;*/
/*}*/

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

::placeholder {
  color: #7F8A99;
}

.row {
  display: flex;
}

.app-container {
  height: 100vh;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}

.nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.content {
  padding-top: 60px;
}

section {
  height: 100vh;
  scroll-snap-align: start;
}

.nav-button {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 25px;
  margin: 12px;
  box-shadow: #cc1fbe 0px 0px 10px;
  border-radius: 13px;
  padding: 12px;
}
