.love {
    position: relative;
    text-align: center;
    color: white;

    .love_page {
        width: 100%;
    }

    .love_text {
        position: absolute;
        top: 50%;
        left: 45%;
        transform: translate(-50%, -50%);
        z-index: 3;
        color: #fcf4f4;
        text-decoration: none;
        font-size: 24px;
        font-weight: bold;

        padding: 10px 20px;
        border-radius: 5px;
        transition: all 0.3s ease;
        transform: rotate(-18deg);
        &:hover {
            color: #b54646;
        }
    }
}
