.album-screen {
  background-color: #ffd1dc;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  position: relative;
  font-family: Arial, sans-serif;

  .title {
    font-size: 90px;
    color: white;
    text-shadow: 2px 2px 0px #ff86c1;
    margin-bottom: 7rem;
  }

  .subtitle {
    color: white;
    text-align: center;
    max-width: 80%;
    margin-bottom: 2rem;
  }

  .image-container {
    display: flex;
    align-items: center;
    justify-content: center;
    //width: 100%;
    margin-bottom: 5rem;

    .image-wrapper {
      display: flex;
      width: 350px;
      height: 350px;
      background-color: #b0c4de;
      padding: 12px;
      border-radius: 10px;
      margin: 0 1rem;

      &.center {
        transform: scale(1.2);
        z-index: 1;
      }

      img {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
    }
  }

  .nav-button {
    background-color: #b0c4de;
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &:hover {
      background-color: #8baed8;
    }
  }

  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 600px;

    .heart-icon {
      font-size: 1.5rem;
      position: relative;

      .bear {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 0.8rem;
      }
    }

    .pagination {
      span {
        margin: 0 0.3rem;
        color: #b0c4de;

        &.active {
          color: #4169e1;
        }
      }
    }
  }

  .corner-hearts {
    position: absolute;
    top: 1rem;
    left: 1rem;
    right: 1rem;
    font-size: 2rem;

    .heart {
      position: absolute;

      &.top-left {
        left: 0;
      }

      &.top-right {
        right: 0;
      }
    }
  }
}
